* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;
}

.camera {
  gap: 20px;
  /* height: 100vh; */
}

.elements {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

.flip {
  cursor: pointer;
}

.photo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

button {
  appearance: none;
  border: none;
  outline: none;
  padding: 10px 20px;

  background-image: linear-gradient(to right, #844FFF 50%, #FF4F84 50%);
  background-position: 0%;
  background-size: 200%;
  color: white;

  border-radius: 7px;
  font-size: 18px;
  font-weight: 600;
  transition: 0.4s;
  cursor: pointer;
}

button:hover {
  background-position: 100%;
}